* {
    padding: 0;
    font-family: 'Lato', sans-serif;
    font-family: 'Roboto', sans-serif;
}

.container {
    display: grid;
    grid-template-rows: 250px auto;
}

.container-select {
    position: relative;
    display: grid;
    text-align: center;
    justify-items: center;
}

.container-select > * {
    position: absolute;
    grid-row: 1;
    text-align: center;
    width: 500px;
    height: 100px;

}

.blockSelect {
    padding-top: 70px;
    pointer-events: none;
    border: 1px black solid;
    border-radius: 10px;
    font-size: larger;
    letter-spacing: 2px;
}

input {
    opacity: 0;
}

.container-image-block {
    grid-row: 2;
    display: grid;
    gap: 5px;
    grid-template-columns: repeat(auto-fill, 200px);
    grid-template-rows: repeat(auto-fill, 300px);
}

.image-block {
    position: relative;

}

.image-block > * {
    position: absolute;

}

img {
    width: 200px;
    border: 1px black solid;
    border-radius: 10px;
}

.fa-times {
    color: red;
    font-size: 25px;
    margin-left: 170px;
}


